
















import { Vue, Prop, Component } from "vue-property-decorator";
const prefixCls = "wu-card__body";
@Component
export default class Card extends Vue {
  @Prop() bodyBorder!: boolean;
  get bodyCls() {
    return [
      prefixCls,
      {
        [`${prefixCls}-border`]: this.bodyBorder
      }
    ];
  }
}
