











import { Vue, Component, Prop, Provide, Emit } from "vue-property-decorator";

const prefixCls = "wu-tabbar-tab";

@Component
export default class WuTabbartab extends Vue {
  @Prop()
  private id!: string;

  get tabbarTabCls() {
    return [
      `${prefixCls}`,
      {
        // @ts-ignore
        [`${prefixCls}-is-selected`]: this.$parent.value === this.id
      }
    ];
  }
}
